        // 
        $(".detail_wapper").addClass('detail_wapper_position')
        $(".wx_bottom_return_mes").removeClass('changeClass').removeClass('changeClass3')
        this.makeImg()

      } else if (type === 'longimg') {
        // 
        $(".detail_wapper").removeClass('detail_wapper_position')
